










import { defineComponent } from '@vue/composition-api'
import { Env } from '@/env'

export default defineComponent({
  components: {},
  setup() {
    const title = process.env.VUE_APP_TITLE
    const commitUrl = Env.commitUrl
    const commitHash7 = Env.commitHash?.substring(0, 7) ?? ''
    return {
      title,
      commitUrl,
      commitHash7,
    }
  },
})
