






import { defineComponent } from '@vue/composition-api'
import { Dialogs } from '@/dialogs'

type Props = {
  data: string
}
export default defineComponent({
  components: {},
  props: {
    data: { type: String, required: true },
  },
  setup(props: Props) {
    const clickQr = async () => {
      await Dialogs.showShareUrl(props.data)
    }
    return {
      clickQr,
    }
  },
})
