
































import { defineComponent, reactive, toRefs, watch } from '@vue/composition-api'
import LayoutPage from '@/components/LayoutPage.vue'
import CameraSelect from '@/components/CameraSelect.vue'
import VideoPreview from '@/components/VideoPreview.vue'
import { useStorePeer } from '@/composables/useStorePeer'
import { MediaConnection } from 'skyway-js'
import { RouterHelper } from '@/router-helper/RouterHelper'
import InputText from '@/components/InputText.vue'
import MessageForm from '@/components/MessageForm.vue'
import { useStoreCamera } from '@/composables/useStoreCamera'
import CameraChangeButton from '@/components/CameraChangeButton.vue'

type State = {}
type Props = {
  apiKey: string
  theirPeerId: string
}
export default defineComponent({
  components: {
    CameraChangeButton,
    MessageForm,
    InputText,
    VideoPreview,
    CameraSelect,
    LayoutPage,
  },
  props: {
    apiKey: { type: String, required: true },
    theirPeerId: { type: String, required: true },
  },
  setup(props: Props) {
    const { mediaStream } = useStoreCamera()
    const { close, yourMediaStream, mediaConnection } = useStorePeer()

    const state = reactive<State>({})
    watch(
      () => mediaConnection.value,
      (value: MediaConnection | null) => {
        if (value === null) {
          alert('')
          RouterHelper.moveTop(props.apiKey)
        }
      },
    )

    return {
      ...toRefs(state),
      mediaStream,
      yourMediaStream,
      close,
    }
  },
})
